



















import Vue from "vue";

import Component from "vue-class-component";
import getCleanValue from "./../ts/getCleanValue";

@Component
export default class Search extends Vue {
  searchFor = "";

  pathTo = "";

  mounted() {
    //this.pathTo = this.$route.name.toLowerCase();
  }

  searchFilter() {
    //this.searchFor = "TODO filter here";
    this.$store.commit("setSearchedFor", this.searchFor);
    // this.$router
    //   .push({ path: this.pathTo, query: { q: this.searchFor } })
    //   .catch((err) => {
    //     console.log(err);
    //   });
  }

  debounce(func, wait) {
    this.searchFilter();

    let timeout: any;

    function executedFunction(...args: any) {
      // callback to be executed
      const later = () => {
        timeout = null; // indicate the debounce ended
        func(...args); // execute the callback
      };

      clearTimeout(timeout); // on every function execution
      timeout = setTimeout(later, wait); // restart the waiting period timeout
    }
  }

  debounceMe() {
    this.debounce(() => {
      // const r = this.$router.resolve({
      //   name: this.$route.name,
      //   params: this.$route.params,
      //   query: this.$route.query
      // });
      // this.$store.commit("setUserRecordCount");
      // window.location.assign(r.href);
    }, 500);
  }

  clearSearch() {
    this.searchFor = "";
    this.$store.commit("setSearchedFor", "");
  }

  cleanName = getCleanValue;
}
